.Location {
  position: relative; }
  .Location iframe {
    width: 100%;
    height: 100vh;
    pointer-events: none; }
  .Location .Info {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    position: absolute;
    bottom: 0;
    width: 80%;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
    padding: 50px 80px;
    background: rgba(229, 229, 229, 0.7);
    text-transform: uppercase; }
    .Location .Info p {
      white-space: pre; }



